@charset "UTF-8";
@font-face {
  font-family: "wooya";
  src: url("fonts/wooya.eot?cdgmyj");
  src: url("fonts/wooya.eot?cdgmyj#iefix") format("embedded-opentype"), url("fonts/wooya.ttf?cdgmyj") format("truetype"), url("fonts/wooya.woff?cdgmyj") format("woff"), url("fonts/wooya.svg?cdgmyj#wooya") format("svg");
  font-weight: normal;
  font-style: normal; }

.wooya-wrapper h2, .wooya-wrapper h3, .wooya-wrapper h4, .wooya-wrapper p {
  color: #293238;
  line-height: 15px;
  letter-spacing: 1px; }

.wooya-wrapper h2 {
  font-size: 25px;
  line-height: 32px;
  text-align: center;
  font-weight: 500; }

.wooya-wrapper h3 {
  font-size: 20px;
  line-height: 26px;
  font-weight: 500; }

.wooya-wrapper p {
  font-size: 12px; }

.wooya-wrapper h2.wooya-files-title:before,
.wooya-wrapper h2.wooya-settings-title:before {
  font-family: "wooya" !important;
  font-size: 21px;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  position: relative;
  top: -15px;
  margin-right: 5px; }

.wooya-wrapper h2.wooya-files-title:before {
  content: ""; }

.wooya-wrapper h2.wooya-settings-title:before {
  content: ""; }

/**
 * Form controls
 */
.wooya-wrapper input[type="checkbox"] {
  border: 3px solid #E6E6E6;
  border-radius: 5px;
  margin-right: 15px;
  margin-top: 1px; }
  .wooya-wrapper input[type="checkbox"]:before {
    margin: -5px 0 0 -6px;
    color: #46D3F2; }

.wooya-wrapper input[type="text"] {
  display: block;
  font-size: 15px;
  font-weight: 300;
  letter-spacing: 1px;
  margin-top: 15px;
  padding: 0 30px;
  height: 41px;
  width: 100%;
  color: #293238;
  background: rgba(255, 255, 255, 0.2) url("images/input-icon.svg") no-repeat 99% 22px;
  border: 1px solid rgba(41, 50, 56, 0.2);
  border-radius: 70.917px; }
  .wooya-wrapper input[type="text"].saving {
    background: rgba(255, 255, 255, 0.2) url("/wp-admin/images/spinner-2x.gif") no-repeat 99% 10px;
    background-size: 20px; }

.wooya-wrapper label {
  color: #293238;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: 1px;
  font-weight: 500; }

.wooya-wrapper .wooya-switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px; }
  .wooya-wrapper .wooya-switch input {
    display: none; }
  .wooya-wrapper .wooya-switch .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(136, 180, 11, 0.2);
    height: 5px;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 34px; }
  .wooya-wrapper .wooya-switch .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 0;
    top: -8px;
    background-color: #88B40B;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 50%; }
  .wooya-wrapper .wooya-switch input:checked + .slider {
    background-color: rgba(252, 108, 108, 0.2); }
  .wooya-wrapper .wooya-switch input:focus + .slider {
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25); }
  .wooya-wrapper .wooya-switch input:checked + .slider:before {
    background-color: #FC6C6C;
    -webkit-transform: translateX(40px);
    -ms-transform: translateX(40px);
    transform: translateX(40px); }
